body {
  background-color: #f4f1fc;
  color: black;
  font-family: system-ui;
}
.hidden {
  display: none;
}
div.notification {
  background-color: #2a2a2a;
  color: snow;
  padding: 10px 140px 5px 10px;
  width: fit-content;
  /* border-bottom: 7.5px solid cornflowerblue; */
  border-radius: 8px;
  position: absolute;
  bottom: 10px;
  left: 10px;
  /* transition: 1.5s ease-in-out transform; */
}
div.notification > span.timer {
  text-align: right;
  position: relative;
  right: -140%;
}
div.notification > span.title {
  font-weight: bold;
  font-size: large;
}
div.notification > div.bar {
  height: 7.5px;
  width: 280.5%;
  background-color: cornflowerblue;
  left: -10;
  position: relative;
  bottom: -5;
  /* border-bottom-right-radius: 8px; */
  border-bottom-left-radius: 8px;
  transition: 0.2s ease-in-out width;
}
